
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,500&display=swap');

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

ul{
  padding: 0px;
  margin: 0px;
}
ul li{
  list-style-type: none;
}
 h1,h2,h3,h4,h5,h6,p{
   margin: 0px;
   padding: 0px;
 }
 a{
   text-decoration: none;
 }
